.media-box {
    width: 20%;
    height: 100%;
    border: .25vh solid #040507;
    cursor: pointer;
}

.media-box .content {
    z-index: 1;
    width: 100%;
    height: 100%;
}

.media-box .preview {
    z-index: -1;
    width: 100%;
    height: 100%;
    transition: all .2s ease-in-out; 
}

.media-box .preview:hover {
    transform: scale(.95); 
    animation: hover_pulse 2s infinite;
}

@keyframes hover_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);

	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}

.media-box h2 {
    font-size: 2.5vh;
    text-align: center;
    color: white;
    padding: 1vh;
}

.media-box h3 {
    font-size: 2vh;
    text-align: center;
    color: gray;
    padding: 1vh;
}