.content-box {
    vertical-align: middle;
    border: .1vh solid #222A37;
    border-radius: .75vh;
    cursor: pointer;
}

.background-image {
    z-index: -1;
    position: relative;
    width: 100%;
    height: 99.5%;
    border: .1vh solid #222A37;
    border-radius: .75vh;
    outline: none;
    margin-top: -9vh;
    opacity: .75;
}

.content-box:hover .background-image{
    animation-fill-mode: forwards;
    display: none;
}

.content-box:hover {
    transform: scale(1.01); 
    animation: content_pulse 2s infinite;
}

.content-box:hover .title-div{
    transform: scale(1.01); 
}

.content-box:hover .endzone-sub-header {
    display: block;
    z-index: -1;
}


.title-div {
    margin-top: 2.5%;
    display: inline-flex;
    justify-content: center;
    margin-left: 5%;
    font-size: 2.5vh;
    color: white;
    border-radius: .75vh;
    border-top: solid .1vh #0984e3;
    border-bottom: solid .1vh #0984e3;
    width: 90%;   
    background-color: rgba(34, 42, 55, .2);
}


h3 {
    font-size: 2.5vh;
}

.endzone-sub-header {
    display: none;
    text-align: center;
    font-size: 2vh;
    color: #cccccc;
    padding: 1vh;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}