body {
  margin: 0;
	font-family: 'Epilogue', sans-serif;
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.top-nav {
  height: 10vh;
  width: 100vw;
  display: flex;
  background-color: #0d1019;
  
}

.top-nav-custom-border {
  width: 100%;
  border-bottom: .5vh solid #353b48;
  transform: scale(1);
	animation: pulse 3s infinite;
}

@keyframes pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);

	}

	70% {
		box-shadow: 0 0 0 .5vh rgba(0, 0, 0, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    border-bottom: .5vh solid #0984e3;
	}
}

.top-nav ul li{
  left: 0;
  list-style: none;
  display: inline-block;
  justify-content: space-evenly;
  margin-right: 2vw;
  margin-top: 1.5vh;
}

.top-nav a{
  text-decoration: none;
  color: white;
  font-size: 2.25vh;
  padding-bottom: .5vh;
  border-bottom: solid .1vh #0984e3;
}

.top-nav a:hover {
  color: #0984e3;
  transition: all .3s ease-in-out;
  border-bottom: solid .1vh white;
}

.top-nav button {
  font-family: 'Epilogue', sans-serif;
  font-weight: 500;
  display: inline-block;
  margin-top: 15%;
  height: 50%;
  background-color: #222A37;
  color: white;
  border: 0;
  border-radius: .75vh;
  font-size: 2vh;
  padding: 1vh 1vw;
  cursor: pointer;
  margin-left: 2vw;
  margin-right: 2vw;

}

.top-nav button:hover {
  transform: scale(1.01); 
  animation: content_pulse 2s infinite;
}

@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}

.top-nav button:hover {
  background-color: #0984e3; 
  transition: all .3s ease-in-out;
  transform: scale(1.1);
}

.top-nav .log-in {
  display: inline-block;
  margin-left: auto;
}

.logo-container {
  width: 20vw;
  height: 10vh;
}

.logo-container .logo {
  width: 100%;
  height: 100%;
  padding: 0,0,0;
}